/* ========== GENERAL STYLES ========== */
body {
  font-size: 13px;
  color: #222;
  background-color: #fff;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}
body.home .head {
  background: 0 0;
  background-size: cover;
  overflow: hidden;
}
body.home .head .overlay {
  padding: 0;
}
body.home .overlay {
  /*background-image: linear-gradient(to right,#207ce5 0,#26a9ff 100%);*/
  background: url("/img/main_top_img.png") no-repeat;
  background-size: 100%;
  display: inline-block;
  position: relative;
  background-size: contain;
  margin: 0 auto;
  width: 100%;
}
body.home .overlay:after {
  padding-top: 56%;
  /* this llama image is 800x600 so set the padding top % to match 600/800 = .75 */
  display: block;
  content: '';
}
body.home .overlay .main-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  color: black;
  text-align: center;
  display: inline-block;
}
body.home .head .navbar.navbar-default {
  background-color: transparent;
  padding-top: 10px;
}
.navbar-default .navbar-brand {
  z-index: 900;
  font-family: 'Pacifico', cursive;
  font-size: 32px;
  line-height: 32px;
  color: #fff;
}
.navbar.navbar-default {
  border: none;
  letter-spacing: 1px;
}
.navbar-default .navbar-nav > li > a {
  color: #FFF;
  font-size: 14px;
}
.navbar.navbar-default .login {
  border: 1px solid #FFF;
  color: #FFF;
  border-radius: 26px;
  padding: 0 1.4em;
  line-height: 40px;
  margin-top: 8px;
}
.intro-well {
  text-align: center;
  padding-top: 70px;
}
.intro-well h1 {
  font-family: 'Lato', sans-serif;
  font-size: 62px;
  line-height: 62px;
  font-weight: 300;
  letter-spacing: 1.5px;
  text-align: center;
  color: #fff;
  margin-bottom: 20px;
  clear: both;
  display: block;
}
.intro-well h2 {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-size: 22px;
  line-height: 32px;
  color: #fff;
  text-align: center;
  margin-bottom: 35px;
  clear: both;
  display: block;
}
.intro-well h1 small {
  color: #FFF;
}
body.home .head .ui-screen img {
  bottom: -100px;
  /*box-shadow: 0 15px 15px rgba(0,0,0,.4);*/
  position: relative;
}
.btn-primary {
  background-color: #106FC1;
}
.btn {
  border-radius: 0;
  border: none;
  font-weight: normal;
  line-height: 24px;
  font-family: 'Open Sans', arial, sans-serif;
}
.features {
  padding-top: 80px;
  padding-bottom: 80px;
}
.cs-media {
  position: absolute;
  left: 20% !important;
  width: 60% !important;
  top: 50% !important;
}
.features-header {
  margin-bottom: 60px;
}
.features {
  text-align: center;
}
.feature i {
