}
.footer .social {
  text-align: right;
}
.footer ul.footer-links {
  font-size: 12px;
  list-style-type: none;
  margin: 0;
  padding: 8px 0 0;
}
.footer ul.footer-links li {
  margin-right: 10px;
  display: inline;
  color: #646464;
}
.footer ul.footer-links li a {
