/* My Styles */
/* face cercuri border-radius: 26px;*/
ul.social-buttons {
  background-color: white;
  height: 44px;
  margin: 0;
}
ul.social-buttons li {
  padding: 0;
}
ul.social-buttons li a {
  background-image: url("/img/icons_sprite.png");
  background-repeat: no-repeat;
  width: 44px;
  height: 44px;
  min-height: 44px;
  margin: 0px;
  padding: 0px;
  display: inline-block;
}
ul.social-buttons li.evaluate a {
  background-position: 0 0;
}
ul.social-buttons li input.evaluate-input {
  border: 0 none;
  font-size: 1.7em;
  font-weight: normal;
  height: 44px;
  padding: 8px 5px 10px 10px;
  width: 190px;
  color: #1e76a9;
  float: right;
}
ul.social-buttons li.fb a {
  background-position: 0 -48px;
  margin-right: 1px;
}
ul.social-buttons li.ok a {
  background-position: 0 -96px;
  margin-right: 1px;
}
ul.social-buttons li.vk a {
  background-position: 0 -144px;
  margin-right: 1px;
}
ul.social-buttons li.btn-google a {
  background-position: 0 -192px;
  margin-right: 1px;
}
ul.social-buttons li.btn-twitter a {
  background-position: 0 -240px;
}
h1.contact-phone {
  font-size: 35px;
  margin-top: 5px;
  color: #063551;
}
/* nav-bar navbar-custom-menu */
.navbar.navbar-default.navbar-custom-menu a.navbar-brand {
  height: auto;
  padding: 0;
  margin-right: 10px;
}
.menu-wrapper {
  background: rgba(255, 255, 255, 0.5);
}
.menu-wrapper > #navbar-collapse ul.navbar-nav li {
  margin: 0 15px;
}
.menu-wrapper > #navbar-collapse ul.navbar-nav li > a {
  padding: 0;
}
.menu-wrapper > #navbar-collapse ul.navbar-nav > :first-child {
  margin-left: 5em;
}
.navbar-custom-menu #navbar-collapse ul.nav li a {
  padding: 0 10px;
  font-size: 17px;
  line-height: 140px;
  color: #063551;
  height: 140px;
}
ul.nav.nav-login {
  margin-right: 0;
}
ul.nav.nav-login > li {
  float: none;
  padding: 0;
  height: 70px;
}
ul.nav.nav-login > li:first-child {
  margin-bottom: 1px;
}
ul.nav.nav-login > li > a,
ul.nav.nav-login > li > button {
  background-image: url("/img/icons_sprite.png");
  background-repeat: no-repeat;
  width: 77px;
  height: 70px;
  margin: 0px;
  padding: 0px;
  display: inline-block;
}
ul.nav.nav-login > li.collapse-btn button {
  float: none;
  border: 0 none;
  border-radius: unset;
}
.row.circles-holder {
  padding-top: 35px;
  padding-bottom: 35px;
}
.row.circles-holder .circle-block {
  border: 1px solid red;
  border-radius: 115px;
  display: inline-block;
  overflow: hidden;
  width: 230px;
  height: 230px;
}
.row.circles-holder .circle-block .circle-inner {
  background-size: 150%;
  height: inherit;
  width: inherit;
}
.row.circles-holder .circle-block .img-terms {
  background: url("/img/terms.png") no-repeat;
}
.row.circles-holder .circle-block .img-quality {
  background: url("/img/quality.png") no-repeat;
}
.row.circles-holder .circle-block .img-result {
  background: url("/img/result.png") no-repeat;
}
.row.circles-holder .title-circle {
  color: #063551;
}
.row.circles-holder .title-circle h2 {
  font-size: 26px;
}
.row.circles-holder .title-circle h4 {
  font-size: 16px;
}
.row.design-holder .title h1,
.projects-holder .title h1,
.preices-holder .title h1 {
  color: #ff7e00;
  font-size: 35px;
}
/* Button design  app-btn btn-orange*/
