  display: inline-block;
  line-height: normal;
}
.app-btn.btn-orange {
  background-color: #ff7e00;
  color: #FFFFFF;
}
.app-btn.btn-orange:hover {
  background-color: #e67100;
  color: #e6e6e6;
  text-decoration: none;
}
/* end button design */
.design-holder-blue {
  background-color: #0a2c40;
  min-height: 415px;
  padding-top: 40px;
  padding-bottom: 40px;
}
.design-holder-blue .question-block h1.bonuses {
