/* Portrait and Landscape */
@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) {
  body.home .overlay {
    background-size: 240%;
    background-position: center top;
  }
  body.home .overlay:after {
    padding-top: 135%;
  }
  .brand-wrapper {
    background: #1E76A9;
  }
  .toggle-menu-wrapper {
    text-align: right;
    background: #1E76A9;
    margin-left: -1px;
  }
  .toggle-menu-wrapper a {
    display: inline-block;
  }
  .toggle-menu-wrapper button {
    float: none;
    border: 0 none;
    border-radius: unset;
    padding: 0;
    margin: 0;
    display: inline-block;
  }
  .toggle-menu-wrapper img {
    display: inline;
  }
  .nav.navbar-nav.navbar-right.social-buttons li.evaluate a {
    display: inline-block;
    width: 15.17241379310345%;
  }
  .nav.navbar-nav.navbar-right.social-buttons li.evaluate input {
    display: inline-block;
    width: 84.82758621%;
  }
  .menu-wrapper #navbar-collapse.navbar-collapse ul.navbar-nav li {
    margin: 0;
  }
  .menu-wrapper #navbar-collapse.navbar-collapse ul.navbar-nav li a {
    line-height: inherit;
    height: inherit;
  }
}
/* Portrait */
@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: portrait) {
  ul.social-buttons {
    margin-right: auto;
    margin-left: auto;
    margin-top: 0;
  }
  ul.social-buttons li {
    float: right;
  }
  ul.social-buttons li > a {
    margin-right: 0;
    margin-left: 1px;
  }
  ul.social-buttons li.evaluate {
    float: left;
    width: 100%;
    margin-bottom: 5px;
  }
  ul.social-buttons li.evaluate > a {
    float: left;
    color: red;
    margin: 0;
  }
  ul.social-buttons li.evaluate > input.evaluate-input {
    width: auto;
  }
  h1.contact-phone {
    font-size: 2em;
    color: white;
    text-shadow: 1px 1px 2px black, 0 0 25px black, 0 0 5px darkgray;
  }
  .navbar.navbar-default.navbar-custom-menu a.navbar-brand {
    margin: 0;
  }
  .navbar-custom-menu .navbar-header a.navbar-brand {
    margin-right: 0;
  }
  .navbar-custom-menu .menu-wrapper {
    margin-left: 0;
    float: none;
  }
  .navbar-custom-menu .menu-wrapper .nav-login {
    float: none;
  }
  .navbar-custom-menu .menu-wrapper .nav-login > li {
    text-align: right;
  }
  .navbar-custom-menu .menu-wrapper .nav-login > li > a {
    max-width: 77px;
    max-height: 70px;
  }
  ul.nav.nav-login > li > a,
  ul.nav.nav-login > li > button {
    background: none;
  }
}
/* Landscape */
@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) {
  ul.social-buttons li {
    float: right;
  }
  ul.social-buttons li.evaluate {
    float: left;
  }
}
