@color-blue: #0a2c40;
@color-blue-text: #063551;
/* ========== GENERAL STYLES ========== */

body {
  font-size: 13px;
  color: #222;
  background-color: #fff;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}
body.home .head {
  background: 0 0;
  background-size: cover;
  overflow: hidden;
}
body.home .head .overlay {
  padding: 0;
}
body.home .overlay {
  /*background-image: linear-gradient(to right,#207ce5 0,#26a9ff 100%);*/
  background: url("/img/main_top_img.png") no-repeat;
  background-size: 100%;

  display: inline-block;
  position: relative;
  background-size: contain;
  margin: 0 auto;
  width: 100%;
}

body.home .overlay:after {
  padding-top: 56%; //56.0546875
  /* this llama image is 800x600 so set the padding top % to match 600/800 = .75 */
  display: block;
  content: '';
}
body.home .overlay .main-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  color: black;
  text-align: center;
  display: inline-block;
  //margin-top: 5%;
}


body.home .head .navbar.navbar-default {
  background-color: transparent;
  padding-top: 10px;
}
.navbar-default .navbar-brand {
  z-index: 900;
  font-family: 'Pacifico', cursive;
  font-size: 32px;
  line-height: 32px;
  color: #fff;
}
.navbar.navbar-default {
  border: none;
  letter-spacing: 1px;
}
.navbar-default .navbar-nav>li>a {
  color: #FFF;
  font-size: 14px;
}
.navbar.navbar-default .login {
  border: 1px solid #FFF;
  color: #FFF;
  border-radius: 26px;
  padding: 0 1.4em;
  line-height: 40px;
  margin-top: 8px;
}
.intro-well {
  text-align: center;
  padding-top: 70px;
}
.intro-well h1 {
  font-family: 'Lato', sans-serif;
  font-size: 62px;
  line-height: 62px;
  font-weight: 300;
  letter-spacing: 1.5px;
  text-align: center;
  color: #fff;
  margin-bottom: 20px;
  clear: both;
  display: block;
}
.intro-well h2 {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-size: 22px;
  line-height: 32px;
  color: #fff;
  text-align: center;
  margin-bottom: 35px;
  clear: both;
  display: block;
}
.intro-well h1 small {
  color: #FFF;
}
body.home .head .ui-screen img {
  bottom: -100px;
  /*box-shadow: 0 15px 15px rgba(0,0,0,.4);*/
  position: relative;
}
.btn-primary {
  background-color: #106FC1;
}
.btn {
  border-radius: 0;
  border: none;
  font-weight: normal;
  line-height: 24px;
  font-family: 'Open Sans', arial, sans-serif;
}
.features {
  padding-top: 80px;
  padding-bottom: 80px;
}
.cs-media {
  position: absolute;
  left: 20% !important;
  width: 60% !important;
  top: 50% !important;
}
.features-header {
  margin-bottom: 60px;
}
.features {
  text-align: center;
}
.feature i {
  font-size: 5em;
  margin-bottom: 15px;
}
.feature h6 {
  font-size: 18px;
}
.feature p {
  font-size: 14px;
  line-height: 1.75;
}
.features-header h2 {
  font-weight: 100;
  font-size: 35px;
}
.footer {
  padding-bottom: 30px;
  padding-top: 50px;
  color: #646464;
}
.footer ul.footer-links {
  font-size: 12px;
  list-style-type: none;
  margin: 0;
  padding: 8px 0 0;
}
.footer ul.footer-links li {
  margin-right: 10px;
  display: inline;
  color: #646464;
}
.footer ul.footer-links li a {
  text-decoration: none;
  color: #646464;
}
.footer .logo {
  text-align: center;
}
.footer .social {
  text-align: right;
}
.footer ul.footer-links {
  font-size: 12px;
  list-style-type: none;
  margin: 0;
  padding: 8px 0 0;
}
.footer ul.footer-links li {
  margin-right: 10px;
  display: inline;
  color: #646464;
}
.footer ul.footer-links li a {
  text-decoration: none;
  color: #646464;
}
.footer .copyright {
  text-align: center;
  font-size: 12px;
  border-top: 1px solid #B6B6B6;
  margin-top: 10px;
  padding-top: 10px;
}

/* My Styles */
/* face cercuri border-radius: 26px;*/
ul.social-buttons {
  background-color: white;
  height: 44px;
  margin: 0;
}
ul.social-buttons li {
  padding: 0;
}
ul.social-buttons li a {
  background-image: url("/img/icons_sprite.png");
  background-repeat: no-repeat;
  width: 44px;
  height: 44px;
  min-height: 44px;
  margin: 0px;
  padding: 0px;
  display: inline-block;
}
ul.social-buttons li.evaluate a {
  background-position: 0 0;
}
ul.social-buttons li input.evaluate-input {
  border: 0 none;
  font-size: 1.7em;
  font-weight: normal;
  height: 44px;
  padding: 8px 5px 10px 10px;
  width: 190px;
  color: #1e76a9;
  float: right;
}
ul.social-buttons li.fb a {
  background-position: 0 -48px;
  margin-right: 1px;
}
ul.social-buttons li.ok a {
  background-position: 0 -96px;
  margin-right: 1px;
}
ul.social-buttons li.vk a {
  background-position: 0 -144px;
  margin-right: 1px;
}
ul.social-buttons li.btn-google a {
  background-position: 0 -192px;
  margin-right: 1px;
}
ul.social-buttons li.btn-twitter a {
  background-position: 0 -240px;
}
h1.contact-phone {
  font-size: 35px;
  margin-top: 5px;
  color: @color-blue-text;
}
/* nav-bar navbar-custom-menu */
.navbar.navbar-default.navbar-custom-menu a.navbar-brand {
  height: auto;
  padding: 0;
  margin-right: 10px;
}

.menu-wrapper {
  background: rgba(255,255,255,0.5);
  //margin-left: 264px;

  > #navbar-collapse ul.navbar-nav {
    li {
      > a {
        padding: 0;
      }
      margin: 0 15px;
    }
    > :first-child {
      margin-left: 5em;
    }
  }
}
.navbar-custom-menu #navbar-collapse ul.nav li a {
  padding: 0 10px;
  font-size: 17px;
  line-height: 140px;
  color: @color-blue-text;
  height: 140px;
}

ul.nav.nav-login {
  margin-right: 0;
  > li {
    float: none;
    padding: 0;
    height: 70px;
    &:first-child {
      margin-bottom: 1px;
    }
    > a, > button {
      background-image: url("/img/icons_sprite.png");
      background-repeat: no-repeat;
      width: 77px;
      height: 70px;
      margin: 0px;
      padding: 0px;
      display: inline-block;
    }

    &.login-btn a {
      //background-position: 0 -288px;
    }
    &.collapse-btn button {
      //background-position: 0 -362px;
      float: none;
      border: 0 none;
      border-radius: unset;
    }
  }
}

.row.circles-holder {
  padding-top: 35px;
  padding-bottom: 35px;

  .circle-block {
    border: 1px solid red;
    border-radius: 115px;
    display: inline-block;
    overflow: hidden;
    //width: 350px;
    //height: 232px
    width: 230px;
    height: 230px;

    .circle-inner {
      background-size: 150%;
      height: inherit;
      width: inherit;
    }

    .img-terms {background: url("/img/terms.png") no-repeat;}
    .img-quality {background: url("/img/quality.png") no-repeat;}
    .img-result {background: url("/img/result.png") no-repeat;}
  } // end circle-block

  .title-circle {
    color: @color-blue-text;
    h2 {
      font-size: 26px;
    }
    h4 {
      font-size: 16px;
    }
  }
} //end .row.circle-holder

.row.design-holder, .projects-holder, .preices-holder {
  .title h1 {
    color: @color-orange;
    font-size: 35px;
  }
}
/* Button design  app-btn btn-orange*/
.app-btn {
  border: 1px solid #FFFFFF;
  font-size: 31px;
  padding: 10px 35px;
  display: inline-block;
  line-height: normal;

  &.btn-orange {
    background-color: @color-orange;
    color: #FFFFFF;
  }
  &.btn-orange:hover {
    background-color: darken(@color-orange, 5%);
    color: darken(#FFFFFF, 10%);
    text-decoration: none;
  }
}
/* end button design */

.design-holder-blue {
  background-color: @color-blue;
  min-height: 415px;
  padding-top: 40px;
  padding-bottom: 40px;

  .question-block {
    h1.bonuses {
      color: @color-orange;
    }
    > p {
      font-size: 16px;
      color: #FFFFFF;
    }
  }


  ul.services-list {
    list-style: none;

    li {
      font-size: 14px;
      color: #FFFFFF;
      line-height: 1.5;
    }

    li:before {
      content: "\002022"; /* � Insert content that looks like bullets */
      padding-right: 8px;
      color: @color-orange; /* Or a color you prefer */
    }
  }
}

.carousel,
.item,
.active {
  height: 100%;
}

.carousel-inner {
  height: 100%;
}

/* Background images are set within the HTML using inline CSS, not here */

.fill {
  width: 100%;
  height: 100%;
  background-position: center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  -o-background-size: cover;
}

/*************** RESET *************************/
/* remove spacing between middle columns */
.no-gutter [class*='col-']:not(:first-child):not(:last-child) {
  padding-right:0;
  padding-left:0;
}
/* remove right padding from first column */
.no-gutter [class*='col-']:first-child {
  padding-right:0;
}
/* remove left padding from first column */
.no-gutter [class*='col-']:last-child {
  padding-left:0;
}
.no-gutter label[class*='col-'].control-label {
  padding-right:5px;
}
