/* ----------- iPhone 5 and 5S ----------- */

/* Portrait and Landscape */
@media only screen
and (min-device-width: 320px)
and (max-device-width: 568px)
and (-webkit-min-device-pixel-ratio: 2) {

  body.home .overlay {
    background-size: 240%;
    background-position: center top;
  }
  body.home .overlay:after {
    padding-top: 135%;
  }
  .brand-wrapper {
    background: #1E76A9;
  }
  .toggle-menu-wrapper {
    text-align: right;
    background: #1E76A9;
    margin-left: -1px;
    a {
      display: inline-block;
    }
    button {
      float: none;
      border: 0 none;
      border-radius: unset;
      padding: 0;
      margin: 0;
      display: inline-block;
    }
    img {
      display: inline;
    }
  } //end togle menu wrapper


  .nav.navbar-nav.navbar-right.social-buttons li.evaluate {
    a {
      display: inline-block;
      width: 15.17241379310345%;
    }
    input {
      display: inline-block;
      width: 84.82758620689655%
    }

  }

  .menu-wrapper {
    #navbar-collapse.navbar-collapse ul.navbar-nav {
      li {
        margin: 0;

        a {
          line-height: inherit;
          height: inherit;
        }
      }
    }
  }

}

/* Portrait */
@media only screen
and (min-device-width: 320px)
and (max-device-width: 568px)
and (-webkit-min-device-pixel-ratio: 2)
and (orientation: portrait) {



  ul.social-buttons {
    margin-right: auto;
    margin-left: auto;
    margin-top: 0;

    & li {
      float: right;

      > a {
        margin-right: 0;
        margin-left: 1px;
      }

      &.evaluate {
        float: left;
        width: 100%;
        margin-bottom: 5px;
        > a {
          float: left;
          color: red;
          margin: 0;
        }
        > input.evaluate-input {
          width: auto;
        }
      }
    }
  }

  h1.contact-phone {
    font-size: 2em;
    color: white;
    text-shadow: 1px 1px 2px black, 0 0 25px black, 0 0 5px darkgray;
  }

  .navbar.navbar-default.navbar-custom-menu a.navbar-brand {
    margin: 0;
  }

  .navbar-custom-menu {
    .navbar-header {
      //float: left;
      //width: 210px;
      //width: 72.5%;
      //margin-left: 0;

      a.navbar-brand {
        margin-right: 0;
      }
    }
    .menu-wrapper {
      margin-left: 0;
      float: none;
      //margin-top: -117px;

      .nav-login {
        float: none;
        > li {
          text-align: right;
          > a {
            max-width: 77px;
            max-height: 70px;
          }
        }
      }


    }
  }

  //end nav custom menu
  ul.nav.nav-login > li > a, ul.nav.nav-login > li > button {
    background: none;
  }


} // end Portrait on iPhones

/* Landscape */
@media only screen
and (min-device-width: 320px)
and (max-device-width: 568px)
and (-webkit-min-device-pixel-ratio: 2)
and (orientation: landscape) {

  ul.social-buttons {
    & li {
      float: right;
      &.evaluate {
        float: left;
      }
    }
  }
}